import React from 'react'
import { Form } from 'antd'
import { ModalProps } from 'antd/lib/modal'
import styled from 'styled-components'
import { length, __, gt, keys, map, pathOr, is } from 'ramda'
import Modal from '@/components/modal'
import { ReportTemplate, PhraseTemplate, SnippetProps } from '@/constants/types'
import { renderSnippets } from '@/components/snippet-utils'
import Button from '@/components/button'
import theme from '../theme'
const { compose } = require('ramda')

interface Props extends ModalProps {
  item?: ReportTemplate | PhraseTemplate | null
}

const SnippetPreviewWrapper = styled.div`
  .ant-card-head {
    min-height: auto;
  }

  .ant-card-head-title {
    padding-top: 0;
  }

  .ant-card-body {
    padding-top: 12px;
    padding-bottom: 8px;
  }
`

export default function SnippetPreviewModal({ item, onCancel }: Props) {
  const [form] = Form.useForm()

  const snippetList = pathOr({}, ['setting'], item)

  return (
    <Modal
      visible
      style={{ top: 30 }}
      maskClosable={false}
      className="ant-modal-header--border-none"
      keyboard={false}
      width="65%"
      onCancel={onCancel}
      footer={[
        <Button key="btn-cancel" color="outline" onClick={onCancel}>
          確認
        </Button>,
      ]}>
      <Form form={form}>
        <SnippetPreviewWrapper>
          {is(Array, snippetList)
            ? renderSnippets()(snippetList as SnippetProps[])
            : compose(
                map((key: string) => (
                  <div key={key}>
                    {compose(
                      gt(__, 0),
                      length,
                      pathOr([], [key])
                    )(snippetList) && (
                      <h2
                        style={{ background: theme.darkGrey, width: '50%' }}
                        className="bg-darkGrey text-white pl-3 my-2">
                        {key}
                      </h2>
                    )}
                    {compose(renderSnippets(), pathOr([], [key]))(snippetList)}
                  </div>
                )),
                keys
              )(snippetList)}
        </SnippetPreviewWrapper>
      </Form>
    </Modal>
  )
}
