import React from 'react'
import { Upload, Tooltip, Button } from 'antd'
import styled from 'styled-components'
import { CloseOutlined, UploadOutlined } from '@ant-design/icons'
import { has } from 'ramda'
import { uuid } from '@/utils/webHelper'
import Input from '@/components/input'

const hasValue = has('value')

class FileUpload extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      fileList: props.value ? [{ uid: uuid(), url: props.value }] : [],
    }
  }

  static getDerivedStateFromProps(nextProps) {
    if (hasValue(nextProps)) {
      // Should be a controlled component.
      if ('value' in nextProps) {
        return typeof nextProps.value === 'string'
          ? { value: nextProps.value }
          : {
              ...(nextProps.value || {}),
            }
      }
    }

    return null
  }

  triggerChange = changedValue => {
    // Should provide an event to pass value to Form.
    const { onChange } = this.props
    if (onChange) {
      onChange(changedValue)
    }
  }

  onRemove = () => {
    this.setState(
      state => ({
        fileList: [],
      }),
      () => this.triggerChange(null)
    )
  }

  customRequest = info => {
    const fileReader = new FileReader()
    fileReader.addEventListener('load', () => {
      const file = {
        uid: uuid(),
        name: info.file.name,
        url: fileReader.result,
        status: 'done',
        file: info.file,
      }

      this.setState(
        state => ({ fileList: [...state.fileList, file] }),
        () => this.triggerChange(this.state.fileList[0])
      )
    })
    fileReader.readAsDataURL(info.file)
  }

  render() {
    const {
      props: { accept, beforeUpload, placeholder },
      state: { fileList = [] },
    } = this

    const hasFile = fileList.length > 0
    const [file] = fileList

    return !hasFile ? (
      <StyledUpload
        multiple={false}
        accept={accept}
        fileList={fileList}
        beforeUpload={beforeUpload}
        onRemove={this.onRemove}
        customRequest={this.customRequest}>
        <Input
          placeholder={placeholder}
          readOnly
          suffix={
            <Tooltip title="點擊選擇上傳檔案">
              <Button
                icon={<UploadOutlined />}
                type="link"
                css={{ marginRight: -10, color: `rgb(183, 183, 183)` }}
              />
            </Tooltip>
          }
        />
      </StyledUpload>
    ) : (
      <Input
        placeholder={placeholder}
        readOnly
        value={file.url.startsWith('data:') ? file.name : file.url}
        suffix={
          <Tooltip title="刪除上傳檔案">
            <Button
              type="link"
              css={{ marginRight: -10, color: `rgb(183, 183, 183)` }}
              data-uid={file.uid}
              icon={<CloseOutlined />}
              onClick={this.onRemove}
            />
          </Tooltip>
        }
      />
    )
  }
}

const StyledUpload = styled(Upload)`
  .ant-upload {
    width: 100%;
  }

  .ant-input-affix-wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }
`

export default FileUpload
