import React from 'react'
import { replace, values, pick } from 'ramda'
import { Row, Col, Tooltip, message } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { EyeOutlined, DeleteOutlined } from '@ant-design/icons'
import { Page } from '@/components/layout'
import Button from '@/components/button'
import Table from '@/components/query-table'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetPreviewModal from '@/components/snippet-preview-modal'
import ImportReportTemplateModal from '@/components/import-template-modal'
import history from '@/utils/history'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb } from '@/hooks/useApp'
import useVisible from '@/hooks/useVisible'
import useModal from '@/hooks/useModal'
import useDelete from '@/hooks/useDelete'
import {
  useReportTemplates,
  useCreateOrUpdateTemplate,
  useDeleteTemplate,
  useReportPreview,
} from '@/services/reportTemplate'
import * as routes from '@/constants/routes'
import { ReportTemplate } from '@/constants/types'

export default function ReportTemplateList() {
  useTitle('結構式報告模版')
  useBreadcrumb([{ name: '報告模版' }, { name: '結構式報告模版' }])

  const { loading, dataSource, refetch } = useReportTemplates()

  const [previewModalVisible, handlePreviewModalVisible, { item }] = useModal({
    content: dataSource.content,
  })

  const [importModalVisible, handleImportModalVisible] = useVisible()

  const [, setIsPreview] = useReportPreview()

  const [, handleAdd] = useCreateOrUpdateTemplate({
    onCompleted: response => {
      message.success(`報告模版${response?.data?.name}已匯入成功`)
      handleImportModalVisible()
      refetch()
    },
  })

  const [, handleDelete] = useDeleteTemplate({
    onCompleted: response => {
      message.success(`報告模版${response?.data?.name}已刪除成功`)
      refetch()
    },
    onError: () => handleDeleteFailure(),
  })

  const { handleConfirmDeletion, handleDeleteFailure } = useDelete({
    content: `刪除後將無法還原，確認要刪除嗎？`,
    dataSource: dataSource.content,
    onRequestDelete: handleDelete,
    refetchQueries: () => refetch(),
  })

  const handleTo = (pathname: string) => () => {
    history.push(pathname)
  }

  const handleToEditPage = (record: ReportTemplate) => () => {
    handleTo(replace(':id', `${record.name}`)(routes.REPORT_TEMPLATE_EDIT))()
  }

  const handlePreview = (evt: React.MouseEvent<HTMLElement>) => {
    setIsPreview()
    handlePreviewModalVisible(evt)
  }

  const handleImport = ({ id, ...values }: ReportTemplate) => {
    handleAdd(pick(['name', 'setting', 'description'], values))
  }

  const columns: ColumnProps<ReportTemplate>[] = [
    {
      title: '報告名稱',
      dataIndex: 'name',
      render: (v, r) => (
        <Button type="link" onClick={handleToEditPage(r)}>
          {v}
        </Button>
      ),
    },
    {
      title: '創作者',
      dataIndex: 'author',
    },
    {
      title: '狀態',
      dataIndex: 'statusName',
    },
    {
      title: '創作時間',
      dataIndex: 'createdAtStr',
    },
    {
      title: '上次修改時間',
      dataIndex: 'updatedAtStr',
    },
    {
      title: '報告描述',
      dataIndex: 'description',
    },
    {
      title: 'actions',
      dataIndex: 'delete',
      align: 'center',
      render: (_, r) => (
        <>
          <Tooltip title="檢視">
            <EyeOutlined
              data-id={r.id}
              className="c-icon text-lg cursor-pointer mr-3"
              onClick={handlePreview}
            />
          </Tooltip>
          <Tooltip title="刪除">
            <DeleteOutlined
              data-id={r.id}
              className="c-icon text-lg cursor-pointer"
              onClick={handleConfirmDeletion}
            />
          </Tooltip>
        </>
      ),
    },
  ]

  return (
    <ReactDndProvider className="is--preview">
      {previewModalVisible && (
        <SnippetPreviewModal item={item} onCancel={handlePreview} />
      )}
      {importModalVisible && (
        <ImportReportTemplateModal
          onOk={handleImport}
          onCancel={handleImportModalVisible}
        />
      )}
      <Page style={{ minHeight: 'calc(100vh - 203px)' }}>
        <Row justify="center">
          <Col span={22}>
            <div>
              <div className="flex justify-end items-center mb-4">
                <Button color="outline" onClick={handleImportModalVisible}>
                  匯入
                </Button>
                <Button
                  color="outline"
                  className="ml-4"
                  onClick={handleTo(routes.REPORT_TEMPLATE_CREATE)}>
                  新增
                </Button>
              </div>
            </div>
            <Table
              rowKey="id"
              columns={columns}
              dataSource={dataSource}
              loading={loading}
            />
          </Col>
        </Row>
      </Page>
    </ReactDndProvider>
  )
}
