import React from 'react'
import { find, path } from 'ramda'

export default function useModal<T, TDetail = T>({
  content,
  key = 'id',
  onToggle,
  ownState,
}: {
  content: T[]
  key?: string
  ownState?: [T | null, React.Dispatch<React.SetStateAction<T | null>>]
  onToggle?: (item: T) => any
}) {
  const [editModalVisible, setEditModalVisible] = React.useState(false)
  const [item, setItem] = ownState || React.useState<T | null>(null) // eslint-disable-line

  const findItem = React.useCallback(
    (evt: React.MouseEvent<HTMLElement>) => {
      return find(
        x =>
          `${path([key], x)}` ===
          (evt.currentTarget as HTMLElement).dataset['id']
      )(content)
    },
    [content, key]
  )

  const handleModalToggle = (evt?: React.MouseEvent<HTMLElement>) => {
    if (!evt) {
      return
    }

    const item = findItem(evt)

    if (!item) {
      setItem(null)
    }

    if (item) {
      setItem(item as T)
    }

    if (onToggle) {
      onToggle(item as T)
    }
  }

  const handleEditModalVisible = (evt?: React.MouseEvent<HTMLElement>) => {
    setEditModalVisible(prev => !prev)
    handleModalToggle(evt)
  }

  return [
    editModalVisible,
    handleEditModalVisible,
    {
      item,
      handleModalToggle,
      setItem,
    },
  ] as [
    boolean,
    (evt?: React.MouseEvent<HTMLElement, MouseEvent> | undefined) => void,
    {
      item: T | null
      setItem: (value: React.SetStateAction<T | null>) => void
      handleModalToggle: (
        evt?: React.MouseEvent<HTMLElement, MouseEvent> | undefined
      ) => void
      itemDetail: TDetail
    }
  ]
}
