import React from 'react'
import { Form } from 'antd'
import { ModalProps } from 'antd/lib/modal'
import Modal from '@/components/modal'
import Button from '@/components/button'
import Input from '@/components/input'
import FileUpload from '@/components/input-file-upload'
import { ReportTemplate } from '@/constants/types'
import { rules, createFormItemLayout } from '@/utils/form'
import { readFile } from '@/utils/webHelper'

interface Props extends Omit<ModalProps, 'onOk'> {
  onOk: (values: ReportTemplate) => void
}

const formItemLayout = createFormItemLayout(4)

export default function ImportTemplateModal({
  onCancel,
  onOk,
  ...props
}: Props) {
  const [form] = Form.useForm()

  const handleOk = () => {
    form.validateFields().then(async values => {
      const { file, ...rest } = values

      const result = await readFile<string>(file.file)

      onOk({ ...JSON.parse(result), ...rest } as ReportTemplate)
    })
  }

  return (
    <Modal
      {...props}
      visible
      title="匯入模版"
      maskClosable={false}
      keyboard={false}
      bodyStyle={{
        paddingLeft: 48,
        paddingRight: 48,
      }}
      width={560}
      footer={[
        <Button
          key="btn-cancel"
          color="outline"
          onClick={onCancel}
          className="mr-3">
          取消
        </Button>,
        <Button key="btn-ok" color="primary" onClick={handleOk}>
          確認
        </Button>,
      ]}
      onCancel={onCancel}>
      <Form hideRequiredMark={false} form={form} colon={false}>
        <Form.Item
          {...formItemLayout}
          label="選擇檔案"
          name="file"
          rules={[rules.required]}>
          <FileUpload accept=".json" placeholder="選擇模版JSON檔案" />
        </Form.Item>
        <Form.Item
          {...formItemLayout}
          label="名稱"
          name="name"
          rules={[rules.required]}>
          <Input />
        </Form.Item>
        <Form.Item
          {...formItemLayout}
          label="創作者"
          name="author"
          rules={[rules.required]}>
          <Input />
        </Form.Item>
        <Form.Item {...formItemLayout} label="描述" name="description">
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}
